  var red = 0;
  var yellow = 0;
  var green = 0;
  function volbok(value) {
    axios
      .get(html + '/pacientesinstavel/' + quotes + value + quotes)
      .then((response) => {
        var x = [0, 1];
        x = response.data;
        setTimeout(() => {
          red = x.length;
          //setXr(red);
          console.log('VERMELHO:' + red);
        }, 2000);
      });
    axios
      .get(html + '/pacientesalerta/' + quotes + value + quotes)
      .then((response) => {
        var x = [0, 1];
        x = response.data;
        setTimeout(() => {
          yellow = x.length;
          //setXy(yellow);
          console.log('AMARELO:' + yellow);
        }, 2000);
      });
    axios
      .get(html + '/pacientesestavel/' + quotes + value + quotes)
      .then((response) => {
        var x = [0, 1];
        x = response.data;
        setTimeout(() => {
          green = x.length;
          //setXg(green);
          console.log('VERDE:' + green);
        }, 2000);
      });
    vbk = [red, yellow, green];
    // setVbk([red, yellow, green]);
  }

